import Vue from 'vue'
import Router from 'vue-router'
import Homepage from '@/components/Homepage'
import Single from '@/components/Single'

import VueFire from 'vuefire'
import firebase from 'firebase/app'
import 'firebase/firestore'

Vue.use(Router)

Vue.use(VueFire)
firebase.initializeApp({
  apiKey: 'AIzaSyCwPHRWTCKQC3UhgIeF6cemb223iTBIhGk',
  authDomain: 'isekainime-a4eef.firebaseapp.com',
  databaseURL: 'https://isekainime-a4eef.firebaseio.com',
  projectId: 'isekainime-a4eef',
  storageBucket: 'isekainime-a4eef.appspot.com',
  messagingSenderId: '487351828465'
})

export const db = firebase.firestore()

const router = new Router({
  routes: [
    {
      path: '/',
      name: 'Homepage',
      component: Homepage
    },
    {
      path: '/anime/:slug/:episode',
      name: 'Single',
      component: Single
    }
  ],
  mode: 'history'
})

export default router



