<template>
  <div>
    <div class="container">
      <div class="columns is-multiline">
        <div class="column">
          <article class="section">
            <div class="title">
              <p># Rilisan Terbaru</p>
            </div>
            <div class="columns is-multiline is-mobile is-variable is-1-mobile">
              <div v-show="!recents.length" class="column has-text-centered">
                <h1>Loading...</h1>
              </div>
              <div class="column is-one-quarter-desktop is-half-mobile" v-for="(recent, idx) in recents" :key="idx" v-show="recents.length">
                <figure class="image is-4by5">
                  <router-link :to="{name:'Single', params:{slug:recent.episode.anime.slug, episode:recent.episode.id}}">
                    <img v-bind:src="recent.episode.anime.preview" alt="Placeholder image">
                  </router-link>
                </figure>
                <div class="has-text-centered">
                  <router-link :to="{name:'Single', params:{slug:recent.episode.anime.slug, episode:recent.episode.id}}">
                    <b><p class="has-text-info">{{recent.episode.anime.title}}</p></b>
                  </router-link>
                  <p>Episode {{recent.episode.id}}</p>
                </div>
              </div>
            </div>
          </article>

          <nav class="pagination is-right" role="navigation" aria-label="pagination">
            <a class="pagination-previous">Previous</a>
            <a class="pagination-next">Next page</a>
            <ul class="pagination-list">
              <li><a class="pagination-link" aria-label="Goto page 1">1</a></li>
              <li><span class="pagination-ellipsis">&hellip;</span></li>
              <li><a class="pagination-link" aria-label="Goto page 45">45</a></li>
              <li><a class="pagination-link is-current" aria-label="Page 46" aria-current="page">46</a></li>
              <li><a class="pagination-link" aria-label="Goto page 47">47</a></li>
              <li><span class="pagination-ellipsis">&hellip;</span></li>
              <li><a class="pagination-link" aria-label="Goto page 86">86</a></li>
            </ul>
          </nav>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '@/router/index'

export default {
  name: 'Homepage',
  data () {
    return {
      recents: []
    }
  },
  methods: {
  },
  created () {
    db.collection('recent').orderBy('id', 'desc').get().then((snapshot) => {
      snapshot.forEach((recent) => {
        let data = {}
        data = recent.data()
        data.episode.get().then(snapshot => {
          data.episode = snapshot.data()
          snapshot.data().anime.get().then(snapshot => {
            data.episode.anime = snapshot.data()
            this.recents.push(data)
          })
        })
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
</style>



