<template>
  <div>
    <div class="container">
      <div class="columns is-multiline">
        <div class="column">
          <article class="section">
            <div class="title">
              <p># Streaming Online</p>
            </div>
              <div class="columns is-multiline is-mobile is-variable is-1-mobile">
                <div v-if="data === null" class="column has-text-centered">
                  <p class="is-size-3">Loading...</p>
                </div>
                <div class="column" v-if="data !== null">
                  <p class="is-size-4">{{data.title}}</p>
                  <p>Episode {{data.episode.id}}</p>
                  <div v-html="data.episode.link">

                  </div>
                </div>
                <!-- <div class="column is-one-quarter-desktop is-half-mobile" v-for="(recent, idx) in recents" :key="idx" v-show="episode.length">
                  <div v-if="recent.episode.count">
                    <figure class="image is-4by5">
                      <router-link :to="{name:'Single', params:{slug:recent.anime.slug, episode:recent.episode.count}}">
                        <img v-bind:src="recent.anime.preview" alt="Placeholder image">
                      </router-link>
                    </figure>
                    <div class="has-text-centered">
                      <router-link :to="{name:'Single', params:{slug:recent.anime.slug, episode:recent.episode.count}}">
                        <b><p class="has-text-info">{{recent.anime.title}}</p></b>
                      </router-link>
                      <p>Episode {{recent.episode.count}}</p>
                    </div>
                  </div>
                </div> -->
              </div>
          </article>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { db } from '@/router/index'

export default {
  name: 'Homepage',
  data () {
    return {
      data: null
    }
  },
  methods: {
  },
  created () {
    db.collection('anime').where('slug', '==', this.$route.params.slug).get().then((snapshot) => {
      snapshot.forEach((doc) => {
        let data = {}
        data = doc.data()
        db.collection('anime/' + this.$route.params.slug + '/episodes').where('id', '==', this.$route.params.episode).get().then(snapshot => {
          snapshot.forEach(eps => {
            data.episode = eps.data()
            this.data = data
          })
        })
      })
    })
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
</style>



