<template>
  <div id="app">

    <header>
      <nav class="navbar is-primary" role="navigation" aria-label="main navigation">
        <div class="navbar-brand">
          <a class="navbar-item" href="/">
            <p class="is-size-4">Isekainime</p>
          </a>

          <a 
          role="button" class="navbar-burger burger" 
          aria-label="menu" aria-expanded="false"
          :class="navbarBurgerClass"
          @click="navbarBurger"
          data-target="navbarItems"
          >
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
            <span aria-hidden="true"></span>
          </a>
        </div>

        <div
        id="navbarItems" 
        class="navbar-menu"
        :class="navbarBurgerClass">
          <div class="navbar-start">
            <a class="navbar-item">
              Home
            </a>

            <a class="navbar-item">
              All Anime
            </a>
            
          </div>

          <div class="navbar-end">
            <div class="navbar-item">
              <div class="buttons">
                <a class="button is-primary">
                  <strong>Tentang Kami</strong>
                </a>
              </div>
            </div>
          </div>
        </div>
      </nav>
    </header>

    <main>
      <router-view/>
    </main>
  </div>
</template>

<script>

const NAVBAR_BURGER_CLASS = {
  true: 'is-active',
  false: ''
}

export default {
  name: 'app',
  data () {
    return {
      navbarBurgerStatus: false
    }
  },
  methods: {
    navbarBurger: function () {
      this.navbarBurgerStatus = !this.navbarBurgerStatus
    }
  },
  computed: {
    navbarBurgerClass: function () {
      return NAVBAR_BURGER_CLASS[this.navbarBurgerStatus]
    }
  }
}
</script>

<style>
  /* header{
    margin-bottom: 25px;
  } */
  .columns {
    width: 100%;
    margin-left: 0px !important;
  }
</style>



